.dashboard {
  &-groups {}

  &-editgroup {
    .form {
      &__week {
        &-title {
          margin-bottom: em(10);
          font-size: em($font-size-h3);
          color: $black;
        }
      }

      &__day {}

      &__event {
        border-bottom: 1px solid $grey;

        &:not(:last-of-type) {
          margin-bottom: em($gutter-y);
        }

        &-title {
          margin-top: em(10);
          margin-bottom: em(10);
          font-size: em($font-size-large);
          color: $black;
        }
      }
    }
  }
}
